.SearchBar {
	background: rgba(0,0,0, .2);
	display:-ms-flexbox;
	display:flex;

	margin: 80px;

	border-radius: 5px;
	overflow:hidden;
	-ms-flex-pack: left;
	    justify-content: left;
	-ms-flex-align: center;
	    align-items: center;


	-webkit-transition: all .1s ease-in-out;


	-o-transition: all .1s ease-in-out;


	transition: all .1s ease-in-out;

}

.SearchBar.focused {

	background: rgba(0,0,0, .4);
}

.SearchBar.focused input {

	width: 600px;
}

.SearchBar svg {

	margin: 20px 30px;
}

.SearchBar input {
	-ms-flex: 1 1;
	    flex: 1 1;
	background: none;
	border:0;
	color: #fff;
	font-size: 3rem;
	margin: 10px;
	outline: none;
	width: 200px;

	-webkit-transition: all .1s ease-in-out;

	-o-transition: all .1s ease-in-out;

	transition: all .1s ease-in-out;
}


